<template>
  <div class="mapwrapper">

      <el-amap vid="amapDemo" :zoom="zoom"  :center="center" class="amap-demo" :plugin="plugin" :lang="'zh_en'">
       
      <div class="toolbar">
        <span v-if="loaded">
          location: lng = {{ lng }} lat = {{ lat }}
        </span>
        <span v-else>loading</span>
      </div>

      </el-amap>

  </div>
</template>

<script>

export default {
  data () {
    let self = this
    return {
        zoom: 15,
        center: [121.5273285, 31.21515044],
        lng: 0,
        lat: 0,
        loaded: false,
        
        plugin: [
          {
            pName: 'ToolBar',
            events: {
              init(instance) {
              }
            }
          },
          {
          pName: 'Geolocation',
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  self.$nextTick();
                }
              });
            }
          }
        }]
    }
  },
  components : {

  }
}

// INSERT INTO room_myapartment
// (price,district,bath_num,desc,facilities,floor,rooms_num,shared,add_time,area,compound,landlord_name,space,type,address,useage) VALUES ('[标签:价格]','[标签:区]','[标签:卫生间数]','[标签:描述]','[标签:设施]','[标签:楼层]','[标签:房间数]','[标签:合租]','[标签:添加时间]','[标签:区域]','[标签:小区名字]','[标签:房东名]','[标签:面积]','[标签:类型]','[标签:地址]','[标签:用途]',)
// INSERT INTO room_myapartmentimages
// (image)
// VALUES ('[标签:图片]')
</script>

<style lang="stylus" scoped>
.mapwrapper
  width 100%
  height 100%

</style>    






